.cards2 {
  padding: 1rem;
  height: 50rem;
  margin-top: 5rem;
  /*background-image: url("../img/BG-Technology.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;*/
}
.gridTechnology {
  padding: 0rem 20% 0rem 20%;
}
.flip-card {
  background-color: transparent;
  width: 100%;
  height: 300px;
  perspective: 1000px;
}
.heading-technology {
  font-family: "Poppins", sans-serif;
  text-align: center;
  color: #4489f1;
  font-weight: 800 !important;
  font-size: 60px;
  line-height: 93.5%;
  margin: 0rem 0rem 0rem 0rem;
  position: relative;
  top: 2rem;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 300px;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-card:focus {
  outline: 0;
}

.flip-card:hover .flip-card-inner,
.flip-card:focus .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 298px;
  backface-visibility: hidden;
}

.flip-card-front {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flip-card-back {
  transform: rotateY(180deg);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imgCard {
  width: 25rem;
}
.MuiGrid-grid-md-4 {
  height: 10rem !important;
}
.card-items3,
.card-items4 {
  position: relative;
  top: -7rem;
}
.card-items5,
.card-items6 {
  position: relative;
  top: -14rem;
}
