.section4 {
  justify-content: center;
}
.centerGrid4 {
  padding: 0rem 10rem 0rem 10rem;
}
.section4-grid {
  padding: 0rem 0rem 0rem 0rem;
}

.section41 {
  display: inline-block;
  position: relative;
  left: 0rem;
}
.gridCurrent {
  border-right: 2px solid white;
}
.gridCurrent,
.gridComing {
  text-align: center;
  background: -webkit-linear-gradient(-45deg, #245db1 5%, #ba64c8 80%);
  -webkit-background-clip: text;
  -webkit-box-decoration-break: clone;
  -webkit-text-fill-color: transparent;
}
.currentAsset,
.comingSoon {
  margin-bottom: 1rem;
}

.section4container1 {
  padding: 0px 5% 50px 5%;
}

.line1,
.line2,
.line3 {
  font-style: normal;
  font-weight: 700;
  font-size: 33px;
  line-height: 124%;
  text-align: left;
  letter-spacing: 0.04em;
  background: -webkit-linear-gradient(-45deg, #245db1 5%, #ba64c8 80%);
  -webkit-background-clip: text;
  -webkit-box-decoration-break: clone;
  -webkit-text-fill-color: transparent;
}

.img41 {
  width: 27rem;
  margin: 2rem 0rem 3rem 0rem;
}

.bottomContainer1 {
  margin: 2rem 0rem 0rem 8rem;
}

.img42,
.img43,
.img44,
.img47,
.img46,
.img48 {
  width: 5rem;
}
.img42:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}
.img43:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}
.img44:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}
.img46:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}
.img47:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}
.img48:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}

.img45 {
  position: relative;
  width: 14rem;
}
.img45:hover {
  transform: scale(1.1);
  transition: 0.5s all ease;
}
.img45container {
  margin-top: 0;
  text-align: right;
  position: relative;
  left: 0rem;
  margin-left: 6rem;
  top: 0rem;
}
.line7,
.line8 {
  width: 8rem;
}
