/* Slider Mobile */

.Mobile-Slider {
  display: none;
}

.containerRoadmap {
  margin-top: 5rem;
  text-align: center;
  padding: 0rem 4rem;
  display: grid;
  grid-template-columns: 85% 15%;
}

.imageRoadmap {
  width: 20rem;
  display: inline-block;
}

.image-containerRoadmap {
  vertical-align: middle;
  display: inline-block;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  width: 20rem;
  margin: auto;
  z-index: 2;
}

.image-containerRoadmap::-webkit-scrollbar {
  display: none;
}
.image-containerRoadmap {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.prevRoadmap {
  padding: 15px 20px 15px 20px;
  cursor: pointer;
  color: white;

  border-radius: 100%;
  transition: 0.6s ease;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 1;
}

.nextRoadmap {
  padding: 15px 20px 15px 20px;
  cursor: pointer;
  color: white;

  border-radius: 100%;
  transition: 0.6s ease;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 1;
}

.prevBtn {
  margin: 10rem 0px 0px 0rem;
}

.nextBtn {
  margin: 4rem 0px 0px 0rem;
}

.btnRoadmap {
  position: relative;
}
