.navbar {
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: relative;
  top: 0;
  z-index: 999;
  background-color: none;
}
.wallet {
  background-color: #242424;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1.5rem;
  height: 3rem;
  position: relative;
  top: 1rem;
  border-radius: 2rem;
  width: max-content;
  color: white;
  font-family: Poppins;
  font-style: bold;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  letter-spacing: 0.15em;
}
.wallet:hover {
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, rgb(19, 29, 56), rgb(66, 35, 91));
}
.icon-wallet {
  width: 2rem;
}
.fa,
.far,
.fas {
  font-family: "Font Awesome 5 Free" !important;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 3px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-right: 20rem;
}

.logo-big {
  height: 3.5rem;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: inline-flex;
  grid-template-columns: repeat(4, auto);
  grid-gap: 5px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 0rem;
  margin-left: 8rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 0.5rem;
  height: 60%;
  position: relative;
  top: 1rem;
  width: max-content;
  color: white;
  font-family: Poppins;
  font-style: bold;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  letter-spacing: 0.15em;
}

.nav-links:hover {
  border-bottom: 2px solid #0064e5;
  transition: all 0.2s ease-out;
}

.fa-bars {
  background: -webkit-linear-gradient(45deg, #354a7e, #3e0f31);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}
@media screen and (max-width: 1320px) {
  .navbar {
    background-color: black;
    height: 60px !important;
  }

  .logo-big {
    height: 2.5rem;

    top: -10px;
    position: relative;
  }
}

@media screen and (max-width: 1320px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 70vh;
    position: absolute;
    top: 58px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    visibility: hidden;
  }

  .nav-item {
    text-align: -webkit-center;
    height: 80px;
  }

  .nav-menu.active {
    visibility: visible;
    background: black;
    left: 0;
    opacity: 1;

    z-index: 1;
    margin-left: 0px !important;
  }

  .nav-links {
    text-align: center;

    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0px;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: -15px;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}
