.section8 {
  padding: 0 0 2rem 0;
}

.section81 {
  text-align: center;
}

.nft-head {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 93.5%;
  text-align: center;
  background: linear-gradient(90.23deg, #314daf 40.11%, #b14ad5 102.92%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.nftContainer {
  padding: 4rem;
  background-image: url("../img/backgroundNFT.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.nft-img {
  width: 25rem;
}
.img-feature {
  width: 8rem;
  position: relative;
}
.img-feature:hover {
  transform: scale(1.1) !important;
  transition: 0.5s all ease;
}
.top-feature {
  top: 3rem;
}
.img-head-feature {
  width: 30rem;
  position: relative;
  top: 3rem;
}
.section82 {
  text-align: center;

  margin: 2rem 7rem;
  padding: 1rem 0rem 9rem 0rem;
}
.headIMG-features {
  text-align: center;
}
.featureContainer {
  text-align: center;
  padding: 3rem 5rem 0rem 5rem;
}
.img-section81 {
  width: 32rem;
}

.img-section82 {
  width: 35rem;
}
.section81btn {
  float: right;
  padding: 0rem 1rem 0rem 0rem;
  position: relative;
  top: 5rem;
}

.btnsection8 {
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, rgb(19, 29, 56), rgb(66, 35, 91));
  color: #ffffff;
  padding: 10px 15px 10px 15px;
  border-radius: 20px;
  cursor: pointer;
}
.section82 {
  margin: 2rem 7rem;
}
.nft-sub-head {
  font-size: 40px;
}
.mob-sec-8 {
  display: none;
}
