.hero-container {
  height: 100%;
  width: 100%;

  display: inherit;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 9rem 4rem 0rem 4rem;
  object-fit: contain;
  margin-bottom: 5rem;
}
.hero-container-mob {
  height: 100%;
  width: 100%;
  display: none !important;
  display: inherit;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 9rem 4rem 0rem 4rem;
  object-fit: contain;
  margin-bottom: 5rem;
}
.videoFrame {
  width: 40rem;
  position: relative;
  z-index: 0;
}
.engine-box {
  margin-left: -2rem;
  margin-top: 4rem;
  position: relative;
  z-index: 0;
}
.diiv {
  margin-top: -100px;
  background-color: black;

  border-radius: 2rem;
}
.text-1 {
  text-align: center;
  margin-left: 5rem;
  margin-top: 7rem;
}
.text {
  width: 25rem;
}

.logo2 {
  margin-top: 3%;
  width: 350px;
}
.textmain,
.Gif,
.GIFBOTTOM {
  text-align: center;
}
.textmain {
  z-index: 1;
  text-align: center;
}

.textMain {
  z-index: 1;

  width: 30rem;
}
.DivGif {
  padding: 2.5rem 2.5rem 0rem 2.5rem;

  width: 33rem;
  height: 30rem;
  position: relative;
  top: 2.5rem;
  left: 8rem;
  z-index: 1;
}
.GIFBOTTOM {
  position: relative;
  z-index: 2;
}
.maingif {
  margin-top: -7rem;
  position: relative;
  left: -5rem;
}

.gifbottom {
  position: absolute;
  margin-top: 29rem;
  width: 25rem;
  left: 11.5rem;
}
.gif2 {
  position: relative;

  z-index: 0;
  margin-left: 5rem;
  margin-top: 2.5rem;
  width: 75%;
}
.Gifbottom2 {
  position: relative;
  z-index: 0;
  left: 2rem;
  top: 8rem;
}
.Gif {
  position: relative;
  top: 15rem;
  left: 1rem;
  z-index: 1;
}
.gif {
  position: relative;
  border-radius: 15rem;
  z-index: 1;
  width: 60%;
}

.MuiGrid-spacing-xs-5 {
  width: 100% !important;
}

.rectangle-50 {
  width: 100%;
  margin-top: 20px;
}

.container-rectangle {
  text-align: center;
  padding-left: 10%;
  padding-right: 10%;
  height: 100%;
}

h3 {
  color: white;
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  text-align: center;
  letter-spacing: 0.15em;
}

h5 {
  color: white;
  font-family: Poppins;
  font-style: bold;
  font-weight: 500;
  font-size: 20px;
  line-height: 135.02%;
  letter-spacing: 0.15em;
}

