.token-utilisation {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 800;
  font-size: 60px;
  line-height: 93.5%;
  /* or 56px */

  text-align: center;

  color: #d9c1ff;
}
.tokenUtilisationImgmob {
  display: none;
}
.section7 {
  margin-top: 10rem;
  margin-bottom: 10rem;
}
.para-tokenUtilisation {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 144.5%;
  text-align: center;
  letter-spacing: -0.03em;
  color: #8b8b8b;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-top: 2rem;
}
.section7img {
  width: 70rem;
}
.section7img:hover {
  transform: scale(1.08) !important;
  transition: 0.5s all ease;
  border: 2px solid white;
  border-radius: 2rem;
}

.tokenUtilisationImg {
  text-align: center;
  margin: 3rem;
}
@media screen and (max-width: 1399px) {
  .token-utilisation {
    font-size: 50px;
  }
  .para-tokenUtilisation {
    font-size: 20px;
  }
  .section7img {
    width: 60rem;
  }
}
@media screen and (max-width: 1199px) {
  .token-utilisation {
    font-size: 40px;
  }
  .para-tokenUtilisation {
    font-size: 16px;
  }
  .section7img {
    width: 50rem;
  }
}
@media screen and (max-width: 899px) {
  .token-utilisation {
    font-size: 35px;
  }
  .para-tokenUtilisation {
    font-size: 13px;
  }
  .section7img {
    width: 40rem;
  }
}
@media screen and (max-width: 699px) {
  .section7 {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .token-utilisation {
    font-size: 30px;
  }
  .para-tokenUtilisation {
    font-size: 10px;
  }
  .section7img {
    width: 30rem;
  }
}
@media screen and (max-width: 599px) {
  .section7 {
    margin-top: 5rem;
  }
  .token-utilisation {
    font-size: 50px;
  }
  .para-tokenUtilisation {
    font-size: 9px;
  }
  .section7img {
    display: none;
  }
  .tokenUtilisationImgmob {
    display: block;
    text-align: center;
  }
  .section7imgmob {
    width: 25rem;
  }
}
@media screen and (max-width: 499px) {
  .section7 {
    margin-top: 5rem;
  }
  .token-utilisation {
    font-size: 40px;
  }
  .para-tokenUtilisation {
    font-size: 9px;
    display: none;
  }
  .section7img {
    display: none;
  }
  .tokenUtilisationImgmob {
    display: block;
    text-align: center;
  }
  .section7imgmob {
    width: 20rem;
  }
}
@media screen and (max-width: 399px) {
  .section7 {
    margin-top: 5rem;
  }
  .token-utilisation {
    font-size: 30px;
  }
  .para-tokenUtilisation {
    font-size: 9px;
    display: none;
  }
  .section7img {
    display: none;
  }
  .tokenUtilisationImgmob {
    display: block;
    text-align: center;
  }
  .section7imgmob {
    width: 15rem;
  }
}
