.footer-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 10rem;
  background-image: url("../img/BGfooter.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.contact {
  padding-top: 2rem;
  padding-bottom: 0rem;
  width: 100%;
}

.contact-us {
  margin-left: 15%;
  margin-right: 15%;

  padding: 15px;
}

.icon1 {
  max-width: 5vw;
  cursor: pointer;
}

.bottom-footer {
  margin-top: 20px;
  width: 100%;
}

.footer {
  text-align: center;
}

.footer-img {
  max-width: 20vw;
}

.iconBox {
  text-align: center;
  position: relative;
  width: 100%;
}
@media screen and (max-width: 1199px) {
  .footer-container {
    padding-top: 6rem;
  }
}
@media screen and (max-width: 699px) {
  .footer-container {
    padding-top: 0rem;
  }
  .contact-us {
    margin-left: 10%;
    margin-right: 10%;
    padding: 15px;
  }
}
@media screen and (max-width: 599px) {
  .footer-container {
    padding-top: 0rem;
  }
  .contact-us {
    margin-left: 7%;
    margin-right: 7%;
    padding: 10px;
  }
}

@media screen and (max-width: 499px) {
  .footer-container {
    padding-top: 0rem;
  }
  .contact-us {
    margin-left: 3%;
    margin-right: 3%;
    padding: 5px;
  }
  .bottom-footer {
    margin-top: 0;
    width: 100%;
  }
}
