@import url("https://fonts.googleapis.com/css?family=Poppins");

.highlishts {
  text-align: center;
  padding: 0rem 10rem 0rem 10rem;
}
.mobile-highlishts {
  display: none;
}
.high {
  text-align: center;
  margin: 1rem;
}
.toke {
  width: 12rem;
}
.iconContainer {
  position: relative;
  z-index: 2;
}
.icon {
  width: 4rem;
}
.imgContent {
  width: 12rem;
}
.imgContent:hover {
  border: 2px solid #4489f1;
  border-radius: 1rem;
}
.iconContainer:hover {
  transform: rotate(0deg) scale(1) translateY(10px);
  transition: 0.5s all ease;
}

